.svg-template {
  display: none; }

.log-in {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 100px 20px 20px 20px; }

.log-in__logo {
  width: 161px;
  height: 161px; }

.log-in__header {
  margin: 40px 0 15px 0;
  font-size: 30px; }

.log-in__note {
  max-width: 230px;
  margin-bottom: 40px;
  font-size: 14px;
  font-weight: 400;
  color: #969595;
  text-align: center; }

.log-in-form {
  display: flex;
  flex-direction: column;
  width: 100%; }

.log-in-form__input {
  width: 100%;
  max-width: 375px;
  height: 50px;
  margin-bottom: 20px;
  padding: 15px;
  font-size: 16px;
  border: 2px solid #adadad;
  border-radius: 10px; }
  .log-in-form__input::placeholder {
    font-size: 16px;
    color: #adadad; }
  .log-in-form__input:focus, .log-in-form__input:hover {
    border-color: #4ea4f6;
    outline: none;
    caret-color: #4ea4f6; }

.log-in-form__input-wrap {
  position: relative; }
  .log-in-form__input-wrap::after {
    position: absolute;
    content: "";
    top: 12px;
    right: 12px;
    width: 24px;
    height: 24px;
    background-image: url("../img/down_svg.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 24px;
    cursor: pointer; }
  .log-in-form__input-wrap--opened::after {
    background-image: url("../img/up_svg.svg"); }

.log-in-form__checkbox {
  display: flex;
  padding: 10px 0 0 50px; }

.log-in-form__label {
  font-size: 16px; }

.countries-list {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  top: 55px;
  left: 0;
  display: none;
  flex-direction: column;
  max-height: 330px;
  background-color: #ffffff;
  border-radius: 7px;
  border: 1px solid #969595;
  overflow-y: scroll; }
  .countries-list--show {
